import $ from 'jquery'
import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import { configRouter } from './router/config' //配置路由的文件
import appWrap from './App'
import appConfig from './setting/appConfig.js'

// 安装路由
Vue.use(VueRouter)

// 安装异步数据控制
Vue.use(VueResource)

// 添加全局变量
Vue.use(appConfig)

// 创建路由
const router = new VueRouter({
  // 哈希模式
  hashbang: true
})
// 配置路由
configRouter(router)

// 设置请求超时;
Vue.http.options.emulateJSON = true;
Vue.http.options.timeout = 60000;

// 过滤器 end

// 创建应用
const App = Vue.extend(appWrap)
router.start(App, 'app')



