<template>
<div class="containers modal fade" id="add-edit-Modal">
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<span data-dismiss="modal" class="close">&times;</span>
					<h5 class="modal-title text-danger">添加</h5>
				</div>
				<div class="modal-body">
					<form id="interfaceConfigForm" class="form-horizontal" name="interfaceConfigForm">
						<div class="form-group">
							<!--.row-->
							<div class="col-xs-3 text-right">
								<label for="appName">名称:</label>
							</div>
							<div class="col-xs-6">
								<input class="form-control" type="text" id="appName"
									name="appName" placeholder="名称" data-type="fullName" />
							</div>
						</div>
						<div class="form-group">
							<!--.row-->
							<div class="col-xs-3 text-right">
								<label for="appId">接入系统ID:</label>
							</div>
							<div class="col-xs-6">
								<input class="form-control" type="text" id="appId" name="appId"
									disabled="disabled" value="adfadfsdf" />
							</div>
						</div>
						<div class="form-group">
							<!--.row-->
							<div class="col-xs-3 text-right">
								<label for="appSecret">授权KEY:</label>
							</div>
							<div class="col-xs-6">
								<input class="form-control" type="text" id="appSecret"
									name="appSecret" disabled="disabled" value="adfadfsdf" />
							</div>
						</div>
						<div class="form-group">
							<div class="col-xs-3 text-right">
								<label for="effect">是否生效:</label>
							</div>
							<div class="col-xs-6">
								<select class="form-control" name="enable" id="effect">
									<option value="0">未生效</option>
									<option value="1">已生效</option>
								</select>
							</div>
						</div>
						<div class="form-group">
							<div class="col-xs-3 text-right">
								<label for="description">描述:</label>
							</div>
							<div class="col-xs-6">
								<textarea class="form-control" rows="3" cols="20"
									placeholder="描述信息" data-type="remarks" name="description"
									id="description"></textarea>
							</div>
						</div>
						<div class="form-group">
							<div class="col-xs-3 text-right"></div>
							<div class="col-xs-3">
								<button type="button" class="btn btn-sm btn-success" id="enter">添加</button>
								<button class="btn btn-sm btn-warning" data-dismiss="modal" >关闭</button>
							</div>
						</div>
					</form>
				</div>
			</div>
		</div>
	</div>
</template>



<script>

 export default{
     ready:function(){
        $('#add-edit-Modal').on('hidden.bs.modal', function () {
            $("#interfaceConfigForm")[0].reset();
        })
     }
 }
 
</script>


