export function configRouter(router) {
  router.map({
    '/': {
      name: "首页",
      component: require('../pages/home.vue'),
    },
    '/authorityMange': {
      name: "鉴权管理",
      component: require('../pages/home.vue'),
      subRoutes: {
        '/interfaceSystem': {
          name: '鉴权系统管理',
          component: require('../pages/interfaceSystem')
        },
        '/interfaceManage': {
          name: '鉴权接口管理',
          component: require('../pages/interfaceManage')
        },
        '/interfaceConfig': {
          name: '鉴权配置管理',
          component: require('../pages/interfaceConfig')
        }
      }
    },

    '*': {
      component: require('../pages/home.vue')
    }
  })

  // alias
  router.alias({
    '/home': '/',
    '/index': '/'
  })

  // redirect
  router.redirect({
  })
}



