<template>
  <el-header></el-header>
  <el-nav :nav-left-hide="hideClass"></el-nav>
  <el-center @click="toggle"></el-center>
  <article class="article">
    <router-view></router-view>
  </article>
</template>

<script>
import header from '../pages/header.vue'
import nav from '../pages/nav.vue'
import center from '../pages/center.vue'

export default{
    components:{
      elHeader:header,
      elNav:nav,
      elCenter:center
    },
    data:function(){
      return {
        hideClass:""
      }
    },
    methods:{
      toggle(){
        !this.hideClass?this.hideClass="hide":this.hideClass="";
      }
    }
}
</script>

<style lang="less">
  .article {
    margin-left: 206px;
    min-height: 600px;
    overflow: hidden;
  }
</style>


