<template>
    <!--<%--添加模态框--%>-->
    <div class="containers modal fade" id="add-edit-Modal">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <span data-dismiss="modal" class="close">&times;</span>
                    <h5 class="modal-title text-danger">添加</h5>
                </div>
                <div class="modal-body">
                    <form id="interfaceSystemForm" class="form-horizontal" name="interfaceSystemForm">
                        <div class="form-group">
                            <!--.row-->
                            <div class="col-xs-3 text-right">
                                <label for="name">名称:</label>
                            </div>
                            <div class="col-xs-6">
                                <input class="form-control" type="text" id="name" name="name" placeholder="名称" data-type="fullName" />
                            </div>
                        </div>
                        <div class="form-group">
                            <!--.row-->
                            <div class="col-xs-3 text-right"><label for="appId">接入系统ID:</label></div>
                            <div class="col-xs-6">
                                <input class="form-control" type="text" id="appId" name="appId" disabled="disabled" value="a65sd4f652342341231" />
                            </div>
                        </div>
                        <div class="form-group">
                            <div class="col-xs-3 text-right"><label for="effect">是否生效:</label></div>
                            <div class="col-xs-6">
                                <select class="form-control" name="enable" id="effect" data-type="canNull">
                <option value="0">未生效</option>
                <option value="1">已生效</option>
              </select>
                            </div>
                        </div>
                        <div class="form-group">
                            <div class="col-xs-3 text-right"></div>
                            <div class="col-xs-3">
                                <button type="button" class="btn btn-sm btn-success" id="enter">确定</button>
                                <button class="btn btn-sm btn-warning" data-dismiss="modal">关闭</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>



<script>

 export default{
     ready:function(){
        $('#add-edit-Modal').on('hidden.bs.modal', function () {
            $("#interfaceSystemForm")[0].reset();
        })
     }
 }
 
</script>


