<template>
    <div class="containers modal fade" id="add-edit-Modal">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <span data-dismiss="modal" class="close" >&times;</span>
        <h5 class="modal-title text-danger">添加</h5>
      </div>
      <div class="modal-body">
        <form id="interfaceManageForm" class="form-horizontal" name="interfaceManageForm">
          <div class="form-group">
            <!--.row-->
            <div class="col-xs-3 text-right">
              <label for="serverName">名　　称:</label>
            </div>
            <div class="col-xs-6">
              <input class="form-control" type="text" id="serverName" name="serverName" placeholder="名称" data-type="fullName" data-type="fullName"/>
            </div>
          </div>
          <div class="form-group">
            <!--.row-->
            <div class="col-xs-3 text-right">
              <label for="serverUrl">接口URL&nbsp;:</label>
            </div>
            <div class="col-xs-6">
              <input class="form-control" type="text" id="serverUrl" name="serverUrl" placeholder="http或https开头" data-type="url"/>
            </div>
          </div>
          <div class="form-group">
            <div class="col-xs-3 text-right">
              <label for="effect">是否生效:</label>
            </div>
            <div class="col-xs-6">
              <select class="form-control" name="enable" id="effect">
                <option value="0">未生效</option>
                <option value="1">已生效</option>
              </select>
            </div>
          </div>
          <div class="form-group">
            <div class="col-xs-3 text-right">
              <label for="methodName">请求方法:</label>
            </div>
            <div class="col-xs-6">
              <select class="form-control" name="methodName" id="methodName">
                <option value="0">get</option>
                <option value="1">post</option>
                <option value="2">put</option>
                <option value="3">delete</option>
              </select>
            </div>
          </div>
          <div class="form-group">
            <div class="col-xs-3 text-right">
              <label for="matchType">匹配方式:</label>
            </div>
            <div class="col-xs-6">
              <select class="form-control" name="matchType" id="matchType">
                <option value="1">通配符</option>
                <option value="2">正则</option>
                <option value="3">全路径</option>
              </select>
            </div>
          </div>
          <div class="form-group">
            <div class="col-xs-3 text-right">
              <label for="description">描　　述:</label>
            </div>
            <div class="col-xs-6">
              <textarea class="form-control" rows="3" cols="20" placeholder="描述信息" data-type="remarks" name="description" id="description"></textarea>
            </div>
          </div>
          <div class="form-group">
            <div class="col-xs-3 text-right"></div>
            <div class="col-xs-3">
              <button type="button" class="btn btn-sm btn-success" id="enter">添加</button>
              <button class="btn btn-sm btn-warning" data-dismiss="modal" >关闭</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</div>
</template>




<script>

 export default{
     ready:function(){
        $('#add-edit-Modal').on('hidden.bs.modal', function () {
            $("#interfaceManageForm")[0].reset();
        })
     }
 }
 
</script>


