<!--面包屑组件-->
<template>
  <div class="crumbs" id="crumbs">
    <ul>
      <li><a href="#/">首页</a><span class="divider"></span><span class="s"> / </span></li>
      <li><a v-link="{path:upClassPath}">{{upClassName}}</a><span class="divider"></span><span class="s"> / </span></li>
      <li class="active"><a v-link="{path:currentPath}">{{currentPathName}}</a></li>
    </ul>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        upClassPath:this.$route.matched[0].handler.path,
        upClassName:this.$route.matched[0].handler.name,
        currentPathName:this.$route.name,
        currentPath:this.$route.path
      }
    }
  }
</script>


<style lang="less">
  .crumbs {
    ul {
      padding: 8px 15px;
      /*margin-bottom: 20px;*/
      list-style: none;
      background-color: #f5f5f5;
      border-radius: 4px;
      li {
        display: inline-block;
        span.s {
          color: #ddd;
        }
      }
    }
  }
</style>


