<template>
  <crumbs></crumbs>
  <div class="container">
    <div class="container-box">
      <div class="btn-positon">
        <a class="btn btn-sm btn-success" id="addAccreditInterface" @click="add()"><i class="icon-plus bigger-110"></i><span
        class="bigger-110 no-text-shadow"> 添加</span></a>
        <button class="btn btn-sm btn-danger" id="delAllBtn"><i class="icon-remove bigger-110"></i><span
        class="bigger-110 no-text-shadow"> 批量删除</span></button>
      </div>
      <table id="interfaceData" class="tableConnStyle table table-striped table-bordered table-hover table-responsive" cellspacing="0" width="100%">
      </table>
    </div>
  </div>
  <!--<%--添加和编辑模态框--%>-->
  <add-edit-modal></add-edit-modal>
  <!--<%--配置--%>-->
  <div class="containers modal fade" id="config-ModalTip">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <div class="modal-header">
          <span data-dismiss="modal" class="close">&times;</span>
          <h5 class="modal-title text-danger">配置</h5>
        </div>
        <div class="modal-body">
          <form id="addUser" class="form-horizontal">

            <div class="form-group">
              <!--.row-->
              <div class="col-xs-3">
                <label for="name" class="sr-only">系统:</label>
                <select class="form-control" name="name" id="name"></select>
              </div>
              <div class="col-xs-3">
                <label for="name" class="sr-only">模块:</label>
                <select class="form-control" name="moduleName" id="moduleName"></select>
              </div>
              <div class="col-xs-3">
                <label for="name" class="sr-only">名称:</label>
                <input class="form-control" type="text" id="systemKey" name="keyName" placeholder="请输入查询名称" />
              </div>
              <div class="col-xs-3">
                <button type="button" class="btn btn-success" id="search">查询</button>
              </div>
            </div>
            <table id="insertInterfaceConfig" class="table table-striped  table-hover">
              <thead>
                <tr>
                  <th><input type="checkbox" name="checkList" value="11" id="modelCheakAll" /></th>
                  <th>名称</th>
                  <th>KEY</th>
                  <th>post方式</th>
                </tr>
              </thead>
              <tbody id="noData">
                <tr>
                  <td colspan="4">没有数据，请点击查询获取数据！...</td>
                </tr>
              </tbody>
            </table>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  
  import crumbs from '../components/crumbs'
  import addEditModal from '../components/add-edit-Model-interfaceManage'
  import "datatables.net"
  import "datatables-bootstrap3-plugin"
  import config from '../setting/appConfig';

  export default{
    components:{
      'crumbs':crumbs,
      'addEditModal':addEditModal,
    },
    data(){
      return {
        ajaxApi:'static/staticData/data.json'
      }
      
    },
    ready(){
        config.dataTableConfig() //dataTable全局配置
        this.datatablesCtrl() //启动datatable
    },
    methods:{
      ajaxTest(){
          this.$http.get(this.$data.ajaxApi).then(function(data){
            console.log(data.data.data)
          },function(res){
              console.log(res)
          })
       
      },
      add(){
        $("#add-edit-Modal").modal({"backdrop":"static"});
        $("#add-edit-Modal h5").html("添加");
        $("#add-edit-Modal #enter").html("添加");
      },   
      datatablesCtrl(){
        $('#interfaceData').dataTable({
          "ajax":{url:"static/staticData/data.json"},
          columns: [
              {data: null, title: '<input type="checkbox" name="checklist" id="checkAll" />'},
              {data: "name", title: "名称"},
              {data: "account", title: "接入系统ID"},
              {data: null, title: "操作"}
            ],
            //设置列的初始值
          columnDefs: [
          {targets: 0,"orderable":false,render: function (data, type, row) {
            return '<input  type="checkbox" name="checkList" value="' + row.id + '"/>'
          }},
          {targets:3,"orderable":false,render: function (data, type, row) {
            return '<button id="edit" class="btn btn-xs btn-warning"  href="javascript:void(0)" value="' + row.id + '"><span class="glyphicon glyphicon-edit"></span> 编辑</button> <button id="config" class="btn btn-xs btn-primary"  href="javascript:void(0)" value="' + row.id + '"><span class="glyphicon glyphicon-cog"></span> 配置</button>'
          }}
    ]})
      }//
    }
  }

  //全选
  $("body").on("click","#checkAll",function(){
    if ($(this).prop("checked") === true) {
            $('#interfaceData tbody input[name="checkList"]').prop("checked", $(this).prop("checked"));
          } else {
            $('#interfaceData tbody input[name="checkList"]').prop("checked", false);
          }
  })
//多选删除
  $("body").on("click",'#delAllBtn',function () {
    
    if ($('#interfaceData tbody tr input[name="checkList"]:checked').length) {
      //common.modalDelEnterTip();
      alert("a")
      $('#delRow').on("click", function () {
        $.each($('#interfaceData tbody tr input[name="checkList"]:checked'), function () {
          var mid = $(this).val();//获取用户选中的value值
          var url = '/sample/api/user/' || '/api/user/';
          $.ajax({
            url: url + mid,
            type: 'DELETE',
            success: function () {
              $('#del-Modal').modal('hide');
              tab.ajax.reload(null, false);
            },
            error: function () {
            }
          });
        })
      })
    } else {
      //common.modalDelTip()
    }
  });

//edit
$("body").on("click","#edit",function(){
  $("#add-edit-Modal").modal({"backdrop":"static"});
  $("#add-edit-Modal h5").html("编辑");
  $("#add-edit-Modal #enter").html("保存");
})
</script>


